<template>
    <div style="display: flex; justify-content: center; padding-top: 10vh; height: 100vh;">
        <el-card style="width: 70vw;height: 18vw;">
            <el-descriptions direction="vertical" :column="4" :size="size" border>
                <el-descriptions-item label="">{{ userinfo.username }}</el-descriptions-item>
                <el-descriptions-item label="">{{ userinfo.phonenum }}</el-descriptions-item>
                <el-descriptions-item label="" :span="2">{{ userinfo.email }}</el-descriptions-item>
                <el-descriptions-item label="">{{ userinfo.campus }}</el-descriptions-item>
                <el-descriptions-item label="">{{ userinfo.address }}</el-descriptions-item>
                <el-descriptions-item label="">{{ toFixed2(userinfo.money) }}</el-descriptions-item>
            </el-descriptions>
            <div style="padding-top: 30px;">
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifyphonenum = true"></el-button>
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifyemail = true"></el-button>
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifycampus = true"></el-button>
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifyaddress = true"></el-button>
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifypassword = true"></el-button>
                <el-button type="primary" class=button size=large style="align-items:end"
                    @click="modifymoney = true"></el-button>
            </div>
        </el-card>
    </div>
    <el-dialog v-model="modifyphonenum" title="" width="30%">
        <el-input v-model="input" placeholder="" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('phonenum')"></el-button>
        </div>
    </el-dialog>
    <el-dialog v-model="modifyemail" title="" width="30%">
        <el-input v-model="input" placeholder="" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('email')"></el-button>
        </div>
    </el-dialog>
    <el-dialog v-model="modifymoney" title="" width="30%">
        <el-input v-model="input" placeholder="" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('money')"></el-button>
        </div>
    </el-dialog>
    <el-dialog v-model="modifycampus" title="" width="30%">
        <el-input v-model="input" placeholder="" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('campus')"></el-button>
        </div>
    </el-dialog>
    <el-dialog v-model="modifyaddress" title="" width="30%">
        <el-input v-model="input" placeholder="" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('address')"></el-button>
        </div>
    </el-dialog>
    <el-dialog v-model="modifypassword" title="" width="30%">
        <el-input v-model="oldPassword" type="password" placeholder="" />
        <el-input v-model="input" type="password" placeholder="" style="padding-top: 10px;" />
        <el-input v-model="confirm" type="password" placeholder="" style="padding-top: 10px;" />
        <div style="display:flex;justify-content: end;padding-top: 10px;">
            <el-button type="primary" class=button size=large @click="modify('password')"></el-button>
        </div>
    </el-dialog>
</template>
<script>
import { ElMessage } from 'element-plus';
export default {
    name: "userInfo",
    data() {
        return {
            userinfo: {
                username: "faqfaq",
                phonenum: "13586012465",
                email: "2654133250@qq.com",
                campus: "",
                address: "",
                money: 114.54545
            },
            modifyphonenum: false,
            modifypassword: false,
            modifyemail: false,
            modifycampus: false,
            modifyaddress: false,
            modifymoney: false,
            input: "",
            confirm: "",
            oldPassword: ""
        }
    },
    methods: {
        toFixed2(str) {
            let num = Number(str);
            return isNaN(num) ? str : num.toFixed(2);
        },
        modify(which) {
            if (which === "password") {
                if (this.input != this.confirm) {
                    ElMessage({ message: "", type: "error" });
                    return;
                }
                this.$http.post("/api/modifypassword", {
                    user_id: this.$store.getters.status.userid,
                    old_pw: this.oldPassword,
                    new_pw: this.input
                }).then(response => {
                    if (response.data.success === true) {
                        ElMessage({ message: "", type: "success" })
                    }
                    else {
                        ElMessage({ message: "", type: "error" })
                    }
                }).catch(error => {
                    ElMessage({ message: error, type: "error" })
                    return;
                });
            }
            else {
                if (which === "phonenum") {
                    this.userinfo.phonenum = this.input;
                    this.modifyphonenum = false;
                }
                else if (which === "email") {
                    this.userinfo.email = this.input;
                    this.modifyemail = false;
                }
                else if (which === "campus") {
                    this.userinfo.campus = this.input;
                    this.modifycampus = false;
                }
                else if (which === "address") {
                    this.userinfo.address = this.input;
                    this.modifyaddress = false;
                }
                else if (which === "money") {
                    var num = parseFloat(this.input)
                    if (isNaN(num)) {
                        ElMessage({ message: "", type: "error" })
                    }
                    else {
                        this.userinfo.money = parseFloat(this.userinfo.money) + num;
                        this.modifymoney = false;
                        this.input = this.userinfo.money
                    }
                }
                this.$http.post("/api/modifyinfo", {
                    user_id: this.$store.getters.status.userid,
                    modify_which: which,
                    modify_content: this.input
                }).then().catch(error => {
                    ElMessage({ message: error, type: "error" })
                    return;
                });
                if(which==="money"){
                    ElMessage({ message: "", type: "success" })
                }
                else{
                    ElMessage({ message: "", type: "success" })
                }

            }
            this.input = "";
            this.confirm = "";
            this.oldPassword = "";
        }
    },
    mounted() {
        this.$http.post('/api/getuserinfo', {
            user_id: this.$store.getters.status.userid,
        }).then(response => {
            this.userinfo = response.data.userinfo
        }).catch(error => {
            ElMessage({ message: error, type: "error" })
        })
    }
}
</script>
<style></style>